import Preloader from "../../../Common/Preloader/Preloader";
import classes from "./ProfileInfo.module.css";
// import ProfileStatus from './ProfileStatus'
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = ({profile, status, updateStatus}) => {

  if (!profile) {
    return <Preloader />
  }

  return (
    <div>
      <div className={classes.pajePhoto}>
        <img alt="img" src="https://upload.wikimedia.org/wikipedia/commons/thumb/d/de/US_Navy_071203-N-8923M-074_An_F-A-18F_Super_Hornet%2C_from_the_Red_Rippers_of_Strike_Fighter_Squadron_%28VFA%29_11%2C_makes_a_sharp_turn_above_the_flight_deck_aboard_the_Nimitz-class_nuclear-powered_aircraft_carrier_USS_Harry_S._Truman.jpg/800px-thumbnail.jpg"></img>
      </div>
      <div className={classes.desctiptionBlock}>
        <div>{profile.fullName}</div>
        <img src={profile.photos.large} alt={`user ${profile.fullName} haven't pic`}/>

        <ProfileStatusWithHooks status={status} updateStatus={updateStatus}/>

      </div>
    </div>
  );
};

export default ProfileInfo;
