import { profileAPI, userAPI } from "../api/api";

const addPostConst = 'ADD-POST';
const DELETE_POST = 'DELETE_POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';

let initialState = {
  posts: [
    { id: 1, postMes: 'Hi, how are you?', likesCount: 12 },
    { id: 2, postMes: 'Hi, how are you?', likesCount: 21 },
    { id: 3, postMes: "Post 3", likesCount: 35 },
    { id: 4, postMes: "Post 4", likesCount: 31 },
    { id: 5, postMes: "Post 5", likesCount: 22 }
  ],
  profile: null,
  status: ""
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {

    case addPostConst:
      let newPost = {
        id: 33,
        postMes: action.messageBody,
        likesCount: 3
      };
      return {
        ...state,
        posts: [...state.posts, newPost],
      }
    case SET_USER_PROFILE:
      return { ...state, profile: action.profile }
    case SET_STATUS:
      return {
        ...state,
        status: action.status
      }

    case DELETE_POST:
      return {
        ...state,
        posts: state.posts.filter(post => post.id !== action.postId)
      }

    default:
      return state;
  }
}

export const addPostAC = (messageBody) => ({ type: addPostConst, messageBody })
export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile })
export const setStatus = (status) => ({ type: SET_STATUS, status })
export const deletePostAC = (postId) => ({ type: DELETE_POST, postId })

export const setProfile = (userId) => async (dispatch) => {
  let response = await userAPI.getUserProfile(userId);
  dispatch(setUserProfile(response.data));
}

export const getStatus = (userId) => async (dispatch) => {
  let response = await profileAPI.getStatus(userId);
  dispatch(setStatus(response.data));
}

export const updateStatus = (status) => async (dispatch) => {
  let response = await profileAPI.updateStatus(status);
  if (response.data.resultCode === 0) {
    dispatch(setStatus(status));
  }
}

export default profileReducer;