import React from "react";
import Post from "./Post/Post";
import { Field, reduxForm } from "redux-form";
import { Textarea } from "../../Common/FormController/FormsController";
import { maxLengthCreator, required } from "../../../utils/validators/validators";

const PostForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field placeholder="textArea" component={Textarea} validate={[maxLengthCreator(5), required]} name={'postTextArea'} ref={props.newPostElement} onChange={props.onChange} value={props.newPostData} />
            </div>

            <div>
                <button>Add post</button>
            </div>

        </form>
    )
}

const PostReduxForm = reduxForm({ form: 'post' })(PostForm);

const MyPost = React.memo((props) => {

    let postElement = props.posts.map(post => <Post postMes={post.postMes} likesCount={post.likesCount} key={post.id} />);

    let onSendPost = (values) => {
        props.sendPost(values.postTextArea);
    }

    return (
        <div>
            <h3>My posts</h3>
            <div>
                <PostReduxForm onSubmit={onSendPost} />
            </div>
            <div>
                {postElement}
            </div>
        </div>
    );
});

export default MyPost;
