import React from "react";
import Profile from "./Profile";
import { connect } from "react-redux";
import { setProfile, getStatus, updateStatus } from "../../redux/profileReducer";
import { useParams } from 'react-router-dom';
import { withAuthRedirect } from "../../hoc/AuthReducer";


export function withRouter(Children) {
    return (props) => {

        const match = { params: useParams() };
        return <Children {...props} match={match} />
    }
}

class ProfileContainer extends React.Component {

    componentDidMount() {
        let userId = this.props.match.params.userId;

        if (!userId) {
            userId = this.props.authorizedUserId;
        }

        this.props.setProfile(userId);
        this.props.getStatus(userId);
    }

    render() {
        return (
                <Profile {...this.props} profile={this.props.profile} status={this.props.status} updateStatus={this.props.updateStatus}/>
        )
    }
}

let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    authorizedUserId: state.auth.id,
    isAuth: state.auth.isAuth
});

let AuthRedirectComponent = withAuthRedirect(ProfileContainer)

let UrlCheckerWithRouter = withRouter(AuthRedirectComponent);

export default connect(mapStateToProps, { setProfile, getStatus, updateStatus })(UrlCheckerWithRouter);