import Paginator from '../Common/Paginator/Paginator';
import User from './User';

let Users = ({ currentPage, selectedPage, userTotalCount, pageSize, onPageChanged, users, ...props }) => {
    return <div>
        <Paginator totalItemsCount={userTotalCount} pageSize={pageSize} onPageChanged={onPageChanged} currentPage={currentPage} selectedPage={selectedPage} />
        <div>
            {
                users.map(user => <User user={user}
                    key={user.id}
                    followingInProgress={props.followingInProgress}
                    unfollow={props.unfollow}
                    follow={props.follow} />
                )
            }
        </div>
    </div>
}

export default Users;