import styles from './Users.module.css';
import offlinePhoto from "../../assets/picture/3-32-scaled-1.jpg";
import { NavLink } from 'react-router-dom';

let User = ({ user, followingInProgress, unfollow, follow }) => {

    return (
        <div>
            <span>
                <div>
                    <NavLink to={'/profile/' + user.id}>
                        <img src={user.photos.small != null ? user.photos.small : offlinePhoto}
                            className={styles.userPhoto} alt="adsa" />
                    </NavLink>
                </div>
                <div>
                    {user.followed
                        ? <button disabled={followingInProgress.some(id => id === user.id)}
                            onClick={() => {
                                unfollow(user.id);
                            }}>unfollow</button>
                        : <button disabled={followingInProgress.some(id => id === user.id)}
                            onClick={() => {
                                follow(user.id);
                            }}>follow</button>

                    }
                </div>
            </span>

            <span>
                <span>
                    <div>{user.name}</div>
                    <div>{user.status}</div>
                </span>


                <span>
                    <div>{"user.location.city"}</div>
                    <div>{"user.location.country"}</div>
                </span>

            </span>
        </div>

    )
}

export default User;