import classes from "./FriendList.module.css";

const FriendList = (props) => {
    return (
      <div className={classes.lastFriendBlock}>
        <div className={classes.friendMainBlock}>
          {props.userName}
          <div className={classes.friendUrl}>
            <img src={props.urlImg} className={classes.friendListImg} alt="?"/>
          </div>
        </div>
      </div>
    );
}

export default FriendList;