import "./App.css";
import HeaderContainer from "./components/Header/HeaderContainer";
// import ProfileContainer, { withRouter } from "./components/Profile/ProfileContainer";
import { Route, Routes } from "react-router-dom";
// import DialogItemContainer from "./components/Dialogs/DialogItemContainer";
import NavbarContainer from "./components/Navbar/NavbarContainer";
import UsersContainer from "./components/Users/UsersContainer";
import Login from "./components/Login/Login";
import SettingContainer from "./components/Settings/SettingContainer";
import NewsContainer from "./components/News/NewsContainer";
import React, { Suspense } from "react";
import { connect } from "react-redux";
import { initializeApp } from "./redux/appReducer";
import { compose } from "redux";
import Preloader from "./components/Common/Preloader/Preloader";
import { withRouter } from "./components/Profile/ProfileContainer";

const DialogItemContainer = React.lazy(() => import("./components/Dialogs/DialogItemContainer"));
const ProfileContainer = React.lazy(() => import("./components/Profile/ProfileContainer"));

class App extends React.Component {

  componentDidMount() {
    this.props.initializeApp();
  }

  render(){

    if(!this.props.initialized){
      return <Preloader />
    }
    

    return (
      <div className="app-wrapper">
        <HeaderContainer />
        <NavbarContainer />
        <div className="app-content-wrap">
        <Suspense fallback={<div><Preloader /></div>}>
          <Routes>
            <Route path="/dialogs/*" element={<DialogItemContainer />} />
            <Route path="/profile/:userId?" element={<ProfileContainer />} />
            <Route path="/users/*" element={<UsersContainer />} />
            <Route path="/settings/*" element={<SettingContainer />} />
            <Route path="/News/*" element={<NewsContainer />} />
            <Route path="/Login/*" element={<Login />} />
          </Routes>
          </Suspense>
          </div>
      </div>
    );
  }
  
};

let mapStateToProps = (state) => ({
  initialized: state.app.initialized
})

export default compose(
  withRouter,
  connect(mapStateToProps, { initializeApp })
)(App);
