import s from './Post.module.css';

const Post = (props) => {

    return (
        <div className={s.item}>
            <img src='https://assets.entrepreneur.com/content/3x2/2000/1682510386-Untitleddesign66.jpg' alt='pilot' />
            {props.postMes}
            <div>
                <span>like</span> {props.likesCount}

            </div>
        </div>
    )
}

export default Post;