import { NavLink } from "react-router-dom";
import classes from "./Navbar.module.css"
import FriendList from "./FriendList/FriendList";

const Navbar = (props) => {

  let friendName = (props.state.dialogsPage.dialogsData.map(friend => <FriendList userName={friend.name} urlImg={friend.url} key={friend.id}/>)).slice(0, 3); // adding key={friend.id}

  return (
    <nav className={classes.nav}>
      <div className={classes.lol}>
        <NavLink to="/profile" className={classes.item} >Profile</NavLink>
        <NavLink to="/dialogs" className={classes.item} >Messages</NavLink>
        <NavLink to="/users" className={classes.item} >users</NavLink>
        <NavLink to="/news" className={classes.item} >News</NavLink>
        <NavLink to="/settings" className={classes.item} >Settings</NavLink>
      </div>
      {friendName}
    </nav>
  );
};

export default Navbar;