const addMessageConst = 'ADD-MESSAGE';

let initialState = {
  messagesData: [
    { message: "Message 1", id: "1" },
    { message: "Message 2", id: "2" },
    { message: "Message 3", id: "3" },
    { message: "Message 4", id: "4" },
    { message: "Message 5", id: "5" }
  ],
  dialogsData: [
    { name: "User 1", id: "1", url: "https://ichef.bbci.co.uk/news/640/amz/worldservice/live/assets/images/2016/11/18/161118124807_pilot_640x360_thinkstock_nocredit.jpg" },
    { name: "User 2", id: "2", url: "https://www.betterteam.com/images/pilot-job-description-3784x2527-2020123.jpeg?crop=4:3,smart&width=1200&dpr=2" },
    { name: "User 3", id: "3", url: "https://i.insider.com/6464c0010d92400018e6c758?width=700" },
    { name: "User 4", id: "4", url: "#" },
    { name: "User 5", id: "5", url: "#" }
  ],
};

const dialogReducer = (state = initialState, action) => {
  switch (action.type) {
    case addMessageConst:
      let newMessage = {
        message: action.messageBody,
        id: 3
      };

      return {
        ...state,
        messagesData: [...state.messagesData, newMessage],
      }

    default:
      return state;
  }
}

export const addMessageAC = (messageBody) => ({type: addMessageConst, messageBody})

export default dialogReducer;